#profile {
  width: 18%;
  height: 18%;
  margin-top: 20px;
}

.nav {
  list-style-type: none;
  margin: 0;
  width: 150%;
  overflow: hidden;
  background-color: rgb(140,0,0);
}

li {
  float: left;
  margin-left: 20px;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  opacity: 70%;
}
.section {
  word-wrap: break-word;
}
#timeline {
  margin-left: -100%;;
}
#body {
  padding: 10px;
  display: flex;
}

h3 {
  font-weight: normal;
}
#logo-polimi {
  width: 60%;
}

td {
  padding: 20px;
  vertical-align: text-top !important;
}

table {
  width: 100%;
}